<template>
   <div class="float"  v-draggable="{ onClick: iconClick }"></div>
   <!-- <div class="float"  v-draggable ></div> -->
  <div class="mainApp" :data-them="currentThem">
    <dialog :open="openWindow" v-if="globData.jsonData" ref="RefDialog">
      <Home v-show="is_txt_prompt_page" :pdata="globData.txt_prompt"
        :graioDoms="{ 'txtdom': graioDoms.txt.dom, 'ntxtdom': graioDoms.ntxt.dom }">
        <div class="main-head">
          <div>
            <button class="btn" @click="openSetting.isopen = !openSetting.isopen"></button>
          </div>
          <div> <span @click="changThem(item)" class="color" v-for="item, index in themCssArr" :key="index"
              :style="{ background: item.bgcolor }"></span>
          </div>
          <div>  <div class="onoff" @click="closeWindow">X</div></div>
     
        </div>


      </Home>
      <Home  v-show="!is_txt_prompt_page" :pdata="globData.img_prompt"
        :graioDoms="{ 'txtdom': graioDoms.img2.dom, 'ntxtdom': graioDoms.nimg2.dom }">
        <div class="main-head">
          <div>
            <button class="btn" @click="openSetting.isopen = !openSetting.isopen"></button>
          </div>
          <div> <span @click="changThem(item)" class="color" v-for="item, index in themCssArr" :key="index"
              :style="{ background: item.bgcolor }"></span>
          </div>
          <div>  <div class="onoff" @click="closeWindow">X</div></div>
     
        </div>
      </Home>
      <div class="settings">
        <Settings :openSetting="openSetting"></Settings>
      </div>

    </dialog>
  </div>


</template>


<script setup>
import { onMounted, ref,inject, watchEffect, watch } from 'vue'
import Home from "../src/components/Home.vue"
import Settings from "@/components/promptRender/Settings.vue"
import { globStore } from '@/stores/index.js'
import romdomJson from '@/randomjson/random1.json';
const $common = inject('common')


const store = globStore()
const { globData } = store
let openWindow = ref(false);
let openSetting = ref({ isopen: false });
 

const transObj = ref(null)
const shortCutOjb = ref(null)

let is_txt_prompt_page = ref(true);

let currentThem = ref('');
let bodyScrollHeight = ref('');
let themCssArr = [
  { cssThem: 'default', bgcolor: '#f97316' },
  { cssThem: 'pink', bgcolor: '#E06C75' },
  { cssThem: 'blue', bgcolor: '#5DADE2' },
  { cssThem: 'green', bgcolor: '#238636' },
  { cssThem: 'yellow', bgcolor: '#FF9800' },
]



const graioDoms = ref({
  txt: { query: '#txt2img_prompt textarea', dom: null },
  ntxt: { query: '#txt2img_neg_prompt textarea', dom: null },
  img2: { query: '#img2img_prompt textarea', dom: null },
  nimg2: { query: '#img2img_neg_prompt textarea', dom: null },
  pagetxt: { query: '#tab_txt2img', dom: null },
  pageimg2: { query: '#tab_img2img', dom: null },


});


function iconClick() {
  openWindow.value  = true; 
  setBodyScroll()
}
function closeWindow() {
  openWindow.value  = false; 
  setBodyScroll()
}

function changThem(item) {
  currentThem.value = item.cssThem;
  document.documentElement.setAttribute('data-them', item.cssThem)
  window.localStorage.setItem('currentThem', item.cssThem)
}

function setBodyScroll() {

  if (openWindow.value) {
    bodyScrollHeight.value = document.documentElement.scrollTop
    window.scrollTo(0, 0);
    document.documentElement.style.overflow = 'hidden';
  }
  else {
    document.documentElement.style.overflow = '';
    window.scrollTo(0, bodyScrollHeight.value);
  }

}


function setTransServer() {
// let postadata=JSON.stringify(transObj.value)
 
fetch('api/sixgod/setTransServer', {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json', 
  },
  body: JSON.stringify(transObj.value), 
 
})
  
}

function loadSetting() {
  shortCutOjb.value = JSON.parse(localStorage.getItem('shortCutOjb'))
  transObj.value = JSON.parse(localStorage.getItem('transObj'))

}

function initShortcut() {
  document.addEventListener('keydown', function (event) {
    let keys = {
      '': null,
      'alt': event.altKey,
      'ctrl': event.ctrlKey,
      'shift': event.shiftKey,
      'commond': event.metaKey
    }

    let _firstKey = keys[shortCutOjb.value.firstKey]
    if (_firstKey) {
      if (event.key.toLowerCase() == shortCutOjb.value.secondKey) {
        openWindow.value = !openWindow.value;
        setBodyScroll()
      }
    }
    else if (event.key.toLowerCase() == shortCutOjb.value.secondKey) {

      if (!shortCutOjb.value.firstKey) {
        openWindow.value = !openWindow.value;
        setBodyScroll()

      }

    }


    if (event.key.toLowerCase() == 'escape') {
      openWindow.value = false;
      setBodyScroll()
    }
  });

}

function getGraioDoms() {
  for (let [key, value] of Object.entries(graioDoms.value)) {
    value.dom = document.querySelector(value.query)
  }

}





function watchPageChange(dom, callback) {
  const observer = new MutationObserver(mutations => {
    mutations.forEach(mutation => {
      if (mutation.attributeName === 'style') {
        const dom = mutation.target;
        const displayValue = window.getComputedStyle(dom).display;
        if (displayValue === 'block') {
          callback && callback()
        }
        // callback&&callback()
      }
    });
  });
  observer.observe(dom, { attributes: true, attributeFilter: ['style'] });

}



function getJSonData() {
  fetch('api/sixgod/getJsonFiles').then(res => res.json()).then(data => {
    globData.jsonData = JSON.parse(data);
    globData.jsonFileNames = Object.keys(globData.jsonData);
    globData.jsonFileNames.forEach(fileName => {
      globData.cssList[fileName] = 0
    })
    globData.prompt_tips=  $common.JsonObjtoArr( globData.jsonData)
  })
}

 

onMounted(() => {
  currentThem.value = window.localStorage.getItem('currentThem') || 'default'
  loadSetting()
  getJSonData();
  initShortcut()
  getGraioDoms()
  setTransServer()
  watchPageChange(graioDoms.value.pagetxt.dom, () => {
    is_txt_prompt_page.value = true;
    globData.is_txt_prompt_page = true;

  })
  watchPageChange(graioDoms.value.pageimg2.dom, () => {
    is_txt_prompt_page.value = false;
    globData.is_txt_prompt_page = false;

  })

  globData.suji_prompt=romdomJson






})














</script>




<style scoped lang="scss">
dialog {
  background-color: var(--body-bg-color);
  color: var(--txt-front-color);
  font-size: var(--base-fontsize);
  width: 100%;
  top: 0;
  left: 0;
  z-index: 999;
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  border: 0px;
  height: 100vh;
  overflow: scroll;



}
.float {
  position: fixed;
  bottom: 50px;
  left: 50px;
  width: 40px;
  height: 40px;
  background: linear-gradient(90deg, #00c9ff, #92fe9d);
  border-radius: 50%;
  color: #4c1178;
  font-size: 22px;
  line-height: 40px;
  font-weight: bold;
  text-align: center;
  cursor: pointer;
  font-family: fangsong;
 
  
}

.color {
  width: 30px;
  height: 30px;
  display: inline-block;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 3px;
}

.main-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1em;
  padding: 1em;
}
.onoff {
  font-size: 20px;
  cursor: pointer;
  width: 20px;
  height: 20px;
  line-height: 20px;
  padding: 5px;

  &:hover {
    color: rgb(196, 4, 4);
    font-weight: bold;
  }
}
</style>
