<template>
    <Dialog @closeDialog="closeDialog" v-if="transObj && shortCutOjb" v-show="props.openSetting.isopen">
        <template v-slot:head>
            <div></div>
        </template>
        <template v-slot:content>

            <div class="setting">
                <div @click="activeTag = item" class="setting-nav" :class="item == activeTag ? 'active' : ''"
                    v-for="item, index in settingNavs" :key="index">{{ item }}</div>
            </div>
            <div class="setting-content">
                <div class="trans-content" v-show="activeTag == ''">
                    <label for="">:</label>
                    <select name="translateServer" v-model="transObj.server">
                        <option value="free"></option>
                        <option value="baidu"></option>
                        <option value="llm"></option>

                    </select>
                    <template v-if="transObj.server == 'baidu'">
                        <label for="">APPID:</label> <input type="text" v-model="transObj.appid">
                        <label for="">:</label> <input type="text" v-model="transObj.secret">
                    </template>
                    <button class="btn" @click="testTransServer"></button><label for="">{{ txt_test_trans }}</label>

                </div>
                <div class="shortcut-key" v-show="activeTag == ''">
                    <span>
                        <label for="">1:</label>
                        <select name="firstKey" v-model="shortCutOjb.firstKey">
                            <option value=""></option>
                            <option value="alt">alt</option>
                            <option value="ctrl">ctrl</option>
                            <option value="commond">commond</option>
                            <option value="shift" selected>shift</option>
                        </select>
                    </span>
                    <span> <label for="">2</label> <input type="text" v-model="shortCutOjb.secondKey"> </span>



                </div>
                <div class="llm form-layout" v-show="activeTag == ''">
                    <p> <label for="">:</label> <input type="text" v-model="transObj.llmName"> </p>
                    <p> <label for="">gpu0~,-1gpu:</label> <input type="text" v-model="transObj.n_gpu_layers"></p>
                    <p> <label for="">temperature:</label> <input type="text" v-model="transObj.temperature"> </p>
                    <p> <label for="">preset:</label> <input type="text" v-model="transObj.preset"> </p>

                </div>
            </div>
            <div class="save"><button @click="saveSettings" class="btn"></button></div>




        </template>
    </Dialog>

</template>

<script setup>
import { onMounted, inject, reactive, ref, watchEffect } from 'vue'
import { globStore } from '@/stores/index.js'
import Dialog from "@/components/promptRender/Dialog.vue"

const props = defineProps({
    openSetting: {
        type: Boolean,
        default: false
    }
})


let activeTag = ref("")
let txt_test_trans = ref("")
const settingNavs = ref(['', '', ''])
const transObj = ref({
    server: 'free',
    appid: '',
    secret: '',
    llmName: 'qwen1_5-4b-chat-q2_k',
    n_gpu_layers: -1,
    temperature: 1.2,
    preset: 'AI'


})
const shortCutOjb = ref({
    firstKey: 'alt',
    secondKey: 'q'
})



function closeDialog() {
    props.openSetting.isopen = false
    txt_test_trans.value = ''
}

function saveSettings(istips = true) {
    localStorage.setItem('shortCutOjb', JSON.stringify(shortCutOjb.value))
    localStorage.setItem('transObj', JSON.stringify(transObj.value))
    setTransServer()
    istips && alert('')
}



function loadSetting() {
    let _shortCutOjb = localStorage.getItem('shortCutOjb')
    let _transObj = localStorage.getItem('transObj')

    if ((!_shortCutOjb || !_transObj)) {
        saveSettings(false)
    }
    else {
        let json_shortCutOjb = JSON.parse(_shortCutOjb)
        let json_transObj = JSON.parse(_transObj)
        if (Object.keys(json_shortCutOjb).length == Object.keys(shortCutOjb.value).length && Object.keys(json_transObj).length == Object.keys(transObj.value).length) {
            shortCutOjb.value = json_shortCutOjb
            transObj.value = json_transObj
        }
        else {
            saveSettings(false)
        }

    }



}

function setTransServer() {
    fetch('api/sixgod/setTransServer', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(transObj.value),
    })

}
function testTransServer() {
    fetch('api/sixgod/testTransServer').then(res => res.json()).then(data => {
        txt_test_trans.value = data
    })
}


watchEffect(() => {


})


onMounted(() => {

    loadSetting()

})

</script>

<style lang="scss" scoped>
.setting {
    display: flex;

    .setting-nav {
        padding: 1em;
        font-size: 1.5em;
        cursor: pointer;
    }
}

.active {
    color: var(--favorite-tags-active-text-color);
}

.setting-content {
    padding: 2em;
    min-height: 100px;

    .trans-content {
        display: grid;
        grid-template-columns: max-content 300px;
        gap: 1em;
    }

    .shortcut-key span {
        margin-right: 1em;
    }


}

.save {
    text-align: center;
    padding: 2em;
}

.form-layout {

 
    p {
        display: flex;
    }
    label {
        flex: 3;
    }
    input {
        flex: 6;
    }
}
</style>